<template>
  <div id="fullpage-container-list-view">
    <transition name="fade"><b-alert :show="banner.show" :variant="banner.type">{{ banner.text }}</b-alert></transition>
    <div id="head-bar-list-view">
      <img src="../assets/toyota_ellipse.svg" class="toyota-ellipse-header">
      <div class="new-project-elem">
        <b-btn v-if="!newProject" variant="outline-danger" @click="newProject = !newProject">Neues Projekt</b-btn>
        <div v-if="newProject" style="display: flex;">
          <b-form-input id="inline-form-input-name" v-model="form.name" placeholder="Projektname" style="flex: 2; margin-right: 0.5em;"></b-form-input>
          <b-btn size="sm" variant="success" @click="addNewProject" style="margin-right: 0.5em;">Los!</b-btn>
          <b-btn size="sm" @click="newProject = !newProject" style="margin-right: 0.5em;">zurück</b-btn>
        </div>
      </div>
      <div class="new-project-elem">
        <b-btn @click="getAllProjects()">refresh</b-btn>
      </div>
    </div>
    <div id="list-body-list-view">
      <b-list-group v-if="!isLoading">
        <b-list-group-item v-for="(project, index) in projects" :key="project.path"> 
          {{ project.name }} 
          <b-btn variant="danger" size="sm" class="list-button" @click="deleteProject(project.path)">Löschen</b-btn>
          <b-btn variant="secondary" size="sm" class="list-button" @click="openProject(project.path)">Öffnen</b-btn>
          <b-btn variant="outline-secondary" size="sm" class="list-button" @click="toggleTagBar(index, project)">Tags</b-btn>
          <b-form-tags v-model="project.tags" v-show="project.showTags" no-outer-focus class="mb-2" style="margin-top: 1em;">
            <template v-slot="{ tags, inputAttrs, inputHandlers, removeTag }">
              <b-input-group aria-controls="my-custom-tags-list">
                <input
                v-bind="inputAttrs"
                v-on="inputHandlers"
                class="form-control">
                <b-input-group-append>
                  <b-button @click="updateTags(project)">Speichern</b-button>
                </b-input-group-append>
              </b-input-group>
              <ul
                  id="my-custom-tags-list"
                  class="list-unstyled d-inline-flex flex-wrap mb-0"
                  aria-live="polite"
                  aria-atomic="false"
                  aria-relevant="additions removals"
              >
                  <!-- Always use the tag value as the :key, not the index! -->
                  <!-- Otherwise screen readers will not read the tag
                       additions and removals correctly -->
                <b-card
                v-for="tag in tags"
                :key="tag"
                :id="`my-custom-tags-tag_${tag.replace(/\s/g, '_')}_`"
                tag="li"
                class="mt-1 mr-1"
                body-class="py-1 pr-2 text-nowrap"
                >
                <strong>{{ tag }}</strong>
                <b-button
                  @click="removeTag(tag)"
                  variant="link"
                  size="sm"
                 :aria-controls="`my-custom-tags-tag_${tag.replace(/\s/g, '_')}_`"
                  ><b-icon-x-circle variant="danger"></b-icon-x-circle></b-button>
                </b-card>
              </ul>
            </template>
          </b-form-tags>
        </b-list-group-item>
      </b-list-group>
      <b-spinner v-if="isLoading" style="margin-left: 45%; margin-top: 20%; width: 5rem; height: 5rem;"></b-spinner>
    </div>
  </div>
</template>

<script>
import store from '../store';
import empty from '../data/empties/emptyContent';
import AuthPost from '../data/requests/AuthPost';

export default {
  data() {
    return {
      projects: [],
      newProject: false,
      isLoading: true,
      isError: false,
      form: {
        name: '',
      },
      banner: {
        show: false,
        type: 'danger',
        text: '',
      },
    };
  },
  methods: {
    setBanner(time, { type, text }) {
      this.banner.type = type;
      this.banner.text = text;
      this.banner.show = true;
      window.setTimeout(() => { this.banner.show = false; }, time);
    },
    toggleTagBar(idx, project) {
      // TDG-Kommentar:
      // In dieser Funktion handle ich das Anzeigen und Ausblenden der Tag-Leiste
      // pro Listenelement. Zunächst hatte ich versucht, im @click vom Tag-Button
      // einfach project.showTags zu flipen. Auf diesen Change reagiert Vue jedoch
      // leider nicht. Daher nutze ich hier die $set method von Vue. Es passiert
      // exakt das selbe, project.showTags wird geflipt, jedoch mit $set extra so
      // das Vue auf den Change reagiert und das DOM aktualisiert
      // vgl: https://stackoverflow.com/questions/69250883/conditional-rendering-inside-v-for-with-vue
      this.$set(this.projects, idx, { ...project, showTags: !project.showTags });
      console.log(this.projects);
      console.log(this.tags);
    },
    async getAllProjects() {
      try {
        this.isLoading = true;
        const res = await fetch(store.state.urlEndpoints.getProjects, {
          ...AuthPost(),
        });

        this.projects = JSON.parse(await res.text());
        this.projects.forEach((project) => { project.showTags = false; });

        console.log(this.projects);
        this.isLoading = false;
      } catch (err) {
        this.setBanner(3000, { type: 'danger', text: 'Fehler beim Laden der Artikel, bitte überprüfen Sie Ihre Internetverbindung.' });
        this.isLoading = false;
      }
    },
    async deleteProject(delPath) {
      if (!window.confirm('Sind Sie sicher? Diese Aktion kann nicht rückgängig gemacht werden!')) return;

      try {
        await fetch(store.state.urlEndpoints.deleteProject, {
          /*
          TDG-Kommentar:
          Was macht AuthPost() hier? AuthPost wird weiter
          oben aus der Datei ../data/requests/AuthPost.js importiert.
          Es handelt sich dabei um eine Funktion, die bei Call ein Objekt returned.
          Dieses Objekt enthält standard-parameter und wird
          hier per spread-syntax in das Param-Objekt von fetch eingebunden.
          So muss ich die Standard-Parameter nur in AuthPost.js ändern,
          und nicht an jeder Stelle im Code, wo sie vorkommen. Aber warum eine Funktion,
          die extra gecalled werden muss, also genauer:
          warum ..AuthPost() und nicht ..AuthPost? Ich könnte in AuthPost.js auch
          einfach ein Objekt exportieren, dass ich dann hier direkt
          importiere und per .. davor "auspacke". Das Problem ist,
          dass die Expressions innerhalb dann aber beim kreieren des Objekts evaluiert werden.
          Zu dem Zeitpunkt ist store.state.userApiToken aber noch leer,
          da der User noch nicht eingeloggt ist. Nach dem Einloggen wird das Objekt
          aber nicht nochmal erstellt, es existiert ja schon. Dem zufogle ist dann
          zwar store.state.userApiToken = <token>, das Feld innerhlab
          AuthPost ist aber noch leer. Daher exportiere ich stattdessen eine Arrow-Function,
          die bei Call das Objekt zurückgibt. Der Call geschieht hier
          durch die () - nach dem Login - das heißt store.state.userApiToken ist bereits
          gesetzt und wird hier korrekt verwendet.
          */
          ...AuthPost(),
          body: JSON.stringify({
            path: delPath,
          }),
        });
      } catch (err) {
        this.setBanner(3000, { type: 'danger', text: 'Fehler beim Löschen des Artikels, bitte überprüfen Sie Ihre Internetverbindung.' });
        this.isLoading = false;
      }
      this.getAllProjects();
    },
    async addNewProject() {
      try {
        this.isLoading = true;
        this.newProject = false;

        const res = await fetch(store.state.urlEndpoints.createNewProject, {
          ...AuthPost(),
          body: JSON.stringify({
            name: this.form.name,
          }),
        });

        const newProject = JSON.parse(await res.text());

        if (newProject.status === 'success') {
          this.setEditorContent(empty(), newProject.path);
        }
        this.isLoading = false;
      } catch (err) {
        this.setBanner(3000, { type: 'danger', text: 'Fehler beim Anlegen eines neuen Artikels, bitte überprüfen Sie Ihre Internetverbindung.' });
        this.isLoading = false;
      }
    },
    async openProject(projectPath) {
      try {
        this.isLoading = true;

        const res = await fetch(store.state.urlEndpoints.readProjectContent, {
          ...AuthPost(),
          body: JSON.stringify({
            path: projectPath,
            isMarkdown: true,
          }),
        });

        const fetchedContent = {
          ...empty,
          text: JSON.parse(await res.text()).content,
        };

        this.setEditorContent(fetchedContent, projectPath);
      } catch (err) {
        this.setBanner(3000, { type: 'danger', text: 'Fehler beim Öffnen des Artikels, bitte überprüfen Sie Ihre Internetverbindung.' });
        this.isLoading = false;
      }
    },
    async updateTags(project) {
      try {
        const res = await fetch(store.state.urlEndpoints.updateTags, {
          ...AuthPost(),
          body: JSON.stringify({
            path: project.name,
            content: project.tags,
          }),
        });

        this.setBanner(3000, JSON.parse(await res.text()).status === 'success' ? { type: 'success', text: 'Gespeichert!' } : { type: 'danger', text: 'Fehler beim Updaten der Tags, bitte überprüfen Sie Ihre Internetverbindung.' });
      } catch (err) {
        this.setBanner(3000, { type: 'danger', text: 'Fehler beim Updaten der Tags, bitte überprüfen Sie Ihre Internetverbindung.' });
      }
    },
    setEditorContent(contentObj, path) {
      store.dispatch('content/patchCurrent', contentObj);
      store.commit('layout/setCanUndo', false);
      store.commit('chooseProject', path);
      this.$router.push('/editor');
    },
  },
  created() {
    this.getAllProjects();
  },
};
</script>


<style scoped>

#fullpage-container-list-view {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: white;
}

#head-bar-list-view {
  position: absolute;
  left: 0;
  width: 100%;
  min-height: auto;
  text-align: center;
  color: black;
  background: #ffff;
  overflow: hidden;
  background-position: center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 6.5em;
  border: 1px solid #e6e6e6;
}

#list-body-list-view {
  position: absolute;
  margin-top: 7.5em;
  margin-left: 15%;
  width: 70%;
  min-height: auto;
  text-align: left;
  overflow: scroll;
  background-position: center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 50em;
}

.toyota-ellipse-header {
  float: left;
  margin-left: 1em;
  margin-top: 1em;
  width: 141px;
  height: 81;
}

.list-button {
  float: right;
  margin-left: 0.5em;
}

.new-project-elem {
  width: auto;
  float: right;
  margin-top: 2em;
  margin-right: 1em;
}

</style>



