<template>
  <modal-inner aria-label="Insert image">
    <div class="image-modal-inner">
      <transition name="fade"><b-alert :show="banner.show" :variant="banner.type">{{ banner.text }}</b-alert></transition>
      <b-tabs>
        <b-tab title="Alle Bilder">
          <image-grid :image-urls="allImages" @chosen="resolve"></image-grid>
        </b-tab>
        <b-tab title="Dieser Artikel" active>
          <image-grid :image-urls="articleBoundImages" @chosen="resolve"></image-grid>
        </b-tab>
        <b-tab title="Hochladen">
          <div style="display: flex; flex-direction: column;">
            <label class="custom-upload-image">
              Laden Sie Ihr Bild hoch...
              <b-form-file class="upload-image" v-model="file1" :state="Boolean(file1)" placeholder="Wählen Sie ein Bild oder ziehen es hier rein..." drop-placeholder="Datei hierher ziehen...">
              </b-form-file>
            </label>
            <span v-if="Boolean(file1)" style="text-align: center; margin-top: 1em; margin-bottom: 1em; font-size: medium;">
              <b>Ausgewählt: </b>{{ file1.name }}
            </span>
            <b-button v-if="Boolean(file1)" style="margin-bottom: 1em; width: 20%; margin-left: 40%;" @click="uploadImageToCloud">
              Hochladen
            </b-button>
          </div>
        </b-tab>
      </b-tabs>
    </div>
  </modal-inner>
</template>

<script>
import modalTemplate from './common/modalTemplate';
import ImageGrid from './ImageGrid';
import MenuEntry from '../menus/common/MenuEntry';
import store from '../../store';

export default modalTemplate({
  components: {
    MenuEntry,
    ImageGrid,
  },
  data: () => ({
    url: '',
    articleImageUrls: [],
    allImageUrls: [],
    file1: null,
    banner: {
      show: false,
      type: 'danger',
      text: '',
    },
  }),
  computed: {
    articleBoundImages() {
      return this.articleImageUrls;
    },
    allImages() {
      return this.allImageUrls;
    },
  },
  methods: {
    setBanner(time, { type, text }) {
      this.banner.type = type;
      this.banner.text = text;
      this.banner.show = true;
      window.setTimeout(() => { this.banner.show = false; }, time);
    },
    //  TDG-Kommentar: Conver2Base64 helper function
    toBase64: file => new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    }),
    resolve(evt, url) {
      evt.preventDefault(); // Fixes https://github.com/benweet/stackedit/issues/1503
      if (!url) {
        this.setError('url');
      } else {
        const { callback } = this.config;
        this.config.resolve();
        callback(url);
      }
    },
    reject() {
      const { callback } = this.config;
      this.config.reject();
      callback(null);
    },
    async uploadImageToCloud() {
      // TDG-Kommentar: Helperfunction für isImage Check
      const getExtension = (filename) => {
        const parts = filename.split('.');
        return parts[parts.length - 1];
      };

      // TDG-Kommentar: isImage checker-function
      const isImage = () => {
        const ext = getExtension(this.file1.name);
        switch (ext.toLowerCase()) {
          case 'jpg':
          case 'gif':
          case 'bmp':
          case 'png':
            return true;
          default: return false;
        }
      };

      // TDG-Kommentar: Wenn Datei größer 1MB
      if (this.file1.size > 1000000) {
        this.setBanner(3000, { type: 'danger', text: 'Die Datei ist zu groß!' });
        return;
      // TDG-Kommentar: Oder kein Image ist, dann nicht hochladen
      } else if (!isImage()) {
        this.setBanner(3000, { type: 'danger', text: 'Die Datei ist kein Bild, bitte laden Sie nur Bilddateien hoch!' });
        return;
      }

      // TDG-Kommentar: Ab hier ist sicher: wir haben ein Bild unter 1MB. Also ab hier upload logic
      try {
        /*  TDG-Kommentar:
            Hier wird das File mit Hilfe der obigen helper-func zu base64 konvertiert.
            Ein Base64-String beginnt immer mit Metaparams
            (File type, coding, etc), gefolgt von dem
            eigentlichen Filestring, getrennt durch ein ','. Da saas.do die
            Metadaten nicht gebrauchen kann
            muss ich daher hier am ersten ',' im String spliten und den rechten
            part nehmen. Daher kommt
            das .split(',')[1] am Ende weiter unten.
        */
        const ImgInBase64 = await this.toBase64(this.file1);
        const postImage = new URL(`${store.state.urlEndpoints.imageEndpoint}postMediaFiles`);
        const params = {
          token: store.state.imageApiToken,
        };

        Object.keys(params).forEach(key => postImage.searchParams.append(key, params[key]));
        //  TDG-Kommentar: ab const postImage bis hier wird das Token als param zur URL hinzugefügt.
        //  Das muss in Zukunft unbedingt geändert werden, das Token gehört in den Header!!!!

        const res = await fetch(postImage, {
          method: 'POST',
          headers: {
            'content-type': 'application/json',
          },
          body: JSON.stringify({
            articleId: store.state.currentFilePath,
            file: ImgInBase64.split(',')[1],
            fileName: this.file1.name,
          }),
        });

        this.setBanner(3000, JSON.parse(await res.text()) ? { type: 'success', text: 'Upload erfolgreich.' } : { type: 'dagner', text: 'Etwas ist schief gelaufen...' });

        this.getAllImages(undefined, true);
      } catch (err) {
        this.setBanner(3000, 'danger', 'Etwas ist schief gelaufen...');
      } finally {
        this.file1 = null;
      }
    },
    async getAllImages(onlyArticle = true, getBoth = false) {
      try {
        const images = [];

        const getImages = new URL(`${store.state.urlEndpoints.imageEndpoint}getToshikoMediaFiles`);

        const params = {
          token: store.state.imageApiToken,
          articleId: onlyArticle ? store.state.currentFilePath : '',
        };

        Object.keys(params).forEach(key => getImages.searchParams.append(key, params[key]));

        const res = await fetch(getImages);

        const parsed = JSON.parse(await res.text());

        if (parsed.success) {
          parsed.mediaFiles.forEach((element) => {
            images.push(element.publicLink);
          });

          if (onlyArticle) {
            this.articleImageUrls = images;
          } else {
            this.allImageUrls = images;
          }

          if (getBoth) this.getAllImages(!onlyArticle, false);
        }
      } catch (err) {
        this.setBanner(3000, 'danger', 'Ein unerwareter Fehler ist aufgetreten.');
      }
    },
  },
  created() {
    //  TDG-Kommentar: Created() ist ein sogn Lifecycle-Hook von vue.
    //  Lifecycle-Hooks erlauben es, an verschiedenen Stellen im Component-Lifecycle
    //  custom code ausführen zu lassen. Hier sorge ich dafür, dass beim Kreieren
    //  dieses Modals im DOM getAllImages() ausgeführt wird um die Bilder zu laden.
    this.getAllImages(undefined, true);
  },
});
</script>

<style scoped>
.image-modal-inner {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.custom-upload-image {
  margin-top: 2em;
  margin-bottom: 2em;
  text-align: center;
  border: 1px solid #ccc;
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
  width: 50%;
  margin-left: 25%;
  height: 4em;
  transition: 0.2s;
}

.custom-upload-image:hover {
  border: 1px solid rgb(133, 132, 132);
}

.upload-image {
  display: none;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-active /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



