







































































































.toyota-ellipse-header {
  margin-left: 1em;
  margin-top: 1em;
  width: 141px;
  height: 81;
}

.full-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f32742;
  color: white;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}

.start-screen {
  top: 0;
  left: 0;
  width: 100%;
  background: #262627;
}

.footer {
  position: relative;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 300px;
  min-height: 300px;
  margin-top: -5px;
}

section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 400px;
  padding-top: 7%;
}



