<template>
  <div>
    <b-row v-for="row in calcNumRows" :key="row">
      <b-col>
        <b-img thumbnail fluid class="image-elem" v-if="imageUrls[3*(row-1)]" :src="imageUrls[3*(row-1)]" :key="imageUrls[3*(row-1)]" @click="$emit('chosen', $event, imageUrls[3*(row-1)])"></b-img>
      </b-col>
      <b-col>
        <b-img thumbnail fluid class="image-elem" v-if="imageUrls[3*(row-1)+1]" :src="imageUrls[3*(row-1)+1]" :key="imageUrls[3*(row-1)+1]" @click="$emit('chosen', $event, imageUrls[3*(row-1)+1])"></b-img>
      </b-col>
      <b-col>
        <b-img thumbnail fluid class="image-elem" v-if="imageUrls[3*(row-1)+2]" :src="imageUrls[3*(row-1)+2]" :key="imageUrls[3*(row-1)+2]" @click="$emit('chosen', $event, imageUrls[3*(row-1)+2])"></b-img>
      </b-col>
    </b-row>
  </div>
</template>

<script>
/*
    TDG-Kommentar: Ich versuche, die Bilder möglichst user-friendly anzuzeigen.
    Oben im template nutze ich ein v-for
    um über alle Bilder in meiner URL Liste zu iterieren.
    Würde ich jetzt einfach ein <img v-for="url in imageUrls" :src="url"> machen,
    würde das zwar klappen, die Bilder
    würden aber alle untereinander angezeigt.
    Ein Grid aus drei Bildern pro Zeile ist da schon besser, das versuche ich hier.
    Mit <b-row> baue ich Zeilen; wenn ich drei Elems pro Zeile anzeigen will, brauche
    ich *Anzahl Bilder* : 3 Zeilen.
    Diesen Wert runde ich auf, da *Anzahl Bilder*
    nicht immer durch 3 teilbar sein muss. Die Berechnung passiert in der
    computed property calcNumRows und wird in
    <b-col> mit v-for="row in calcNumRows" gereferenced.

    Jetzt muss noch für jede Zeile in Abhängigkeit von
    row das richtige Bild in die richtige
    <b-col> geladen werden. Für die erste Zeile wäre das also
    imageUrls[0], imageUrls[1] und imageUrls[2]. Für die zweite
    dann imageUrls[3], imageUrls[4] und imageUrls[5]... und so weiter...
    Mathematisch wäre das in Abhängigkeit zu
    row dann imageUrl[3*row], imageUrls[3*row+1] und imageUrls[3*row+2].
    Nur leider zählt Vue aus irgend einem Grund von 1 und nicht von 0 aus;
    row startet also bei 1. Daher muss es dort überall (row-1) heißen.

    Das v-if in den img tags ist notwendig, da calcNumRows
    und damit auch max_possible(row) >= imageUrls.length,
    es muss also nicht unbedingt ein imageUrls an jeder Stelle
    geben. Damit hier kein Fehler auftritt,
    wird das Element in diesem Fall erst gar nicht gerendert.
  */
export default {
  props: {
    imageUrls: {
      type: Array,
    },
  },
  computed: {
    calcNumRows() {
      return Math.ceil(this.imageUrls.length / 3);
    },
  },
};
</script>

<style scoped>
.image-elem {
  margin-top: 1em;
  box-shadow: -4px -4px -4px -2px rgb(90, 90, 90);
  transition: 0.2s;
}

.image-elem:hover {
  filter: grayscale(60%);
  transform: translate(10px, 10px);
}
</style>


